import './index.less';
import {isElementVisible} from '../../../../lib/utils/index';
export default {
    template: require('./index.html'),
    initData() {
        return {
            majorPrograms: {},
            muted: false
        };
    },
    videoClick() {
        const cmd = this.data.get('majorPrograms').cmd;
        this.fire('clickInvoke', cmd);
    },
    muteClick() { // 
        let videoRef = this.ref('video');
        let muteStatus = this.data.get('muted');
        videoRef.muted = !muteStatus;
        this.data.set('muted', !muteStatus);
    },
    attached() {
        let ticking = false;
        let videoRef = this.ref('video');
        let videoPlay = videoRef.play();
        videoPlay.then((res) => {
            // 
            console.log(res);
        }).catch((err) => {
            /**
             * weixin
             * 
             */
            console.log(err);
            // weixin
            document.addEventListener(
                'WeixinJSBridgeReady',
                e => videoRef.play(),
                false
            );
            // 
            videoRef.muted = true;
            videoRef.play();
            // .play()
            window.addEventListener('touchstart', e => videoRef.play());
        });
        window.addEventListener('scroll', (e) => {
            if (!ticking) {
                window.requestAnimationFrame(() => {
                    isElementVisible(videoRef)
                        ? videoRef.play()
                        : videoRef.pause();
                    ticking = false;
                });
                ticking = true;
            }
        });
        window.addEventListener('popstate', (event) => {
            videoRef.currentTime = 0;
            videoRef.play();
        });
    }
};