const DEFAULT_CONFIG = {
  // 
  app: '',
  // 
  url: 'https://activity.baidu.com/activity/felog/error',
  // 
  watch: true
};

class H5ErrLog {
  constructor() {
    // 
    this.config = {}; // 

    this.isCheckedParams = true;
  }

  init(config = {}) {
    this.config = Object.assign({}, DEFAULT_CONFIG, config);

    if (!this.config.app) {
      this.isCheckedParams = false;
      console.error('[h5-error-log]: app~');
      return;
    } // isCheckedParamsinit


    this.isCheckedParams = true;

    if (this.config.watch) {
      this.watch();
    }

    delete this.config.watch;
  }
  /**
   * 
   */


  watch() {
    window.addEventListener('error', (...args) => {
      this.send({
        level: 'error',
        type: 'page',
        content: JSON.stringify(args),
        ext: {
          errmsg: JSON.stringify(args)
        }
      });
    }); // catch

    window.addEventListener('unhandledrejection', event => {
      event.preventDefault();
      this.send({
        level: 'unhandledrejection',
        type: 'page',
        content: JSON.stringify(event.reason),
        ext: {
          errmsg: JSON.stringify(event.reason)
        }
      });
    });
  }
  /**
   * 
   *
   * @param {Object} errInfo 
   * @param {string} errInfo.level  info/error
   * @param {string} errInfo.type  net/app/api/...
   * @param {string} errInfo.content 
   * @param {Object} errInfo.ext 
   * @param {string} errInfo.ext.errmsg 
   * @param {boolean} useSendbeacon Sendbeacon
   */


  send(errInfo = {
    level: 'info',
    type: '-',
    content: '-',
    ext: {
      errmsg: ''
    }
  }, useSendbeacon = false) {
    if (!this.isCheckedParams) {
      console.error('[h5-error-log]: init');
      return;
    }

    let data = { ...this.config,
      ...errInfo
    };
    data.ext = JSON.stringify(data.ext || {});
    data.h5 = '1';
    delete data.url;
    let queryString = toQueryString(data); // sendBeaconloadImage

    if (!useSendbeacon || useSendbeacon && !this.sendBeacon(queryString)) {
      this.loadImage(queryString);
    }
  }
  /**
   * 
   *
   * @param {string} query get
   */


  loadImage(query) {
    let img = new Image();
    let imgId = '_img_' + getId();
    window[imgId] = img;

    img.onload = img.onerror = img.onabort = function () {
      img = img.onload = img.onerror = img.onabort = null;
      delete window[imgId];
    };

    img.src = this.config.url + '?' + query;
  }
  /**
   * sendBeacon
   * @param {string} query 
   */


  sendBeacon(query) {
    if ('sendBeacon' in navigator) {
      return navigator.sendBeacon(`${this.config.url}?${query}`);
    }

    return false;
  }

} // new


var index = new H5ErrLog(); // 

export default index;
export { H5ErrLog };
